.bid-request-form-container {

  .allocation-form-container {
    padding: 20px 50px 0;
    border: 1px solid #ECECEC;
    border-radius: 5px;
    margin-bottom: 30px;
    position: relative;

    & > .close-btn {
      font-size: 25px;
      position: absolute;
      right: 5px;
      top: -4px;
      font-weight: bolder;
      cursor: pointer;
      color: #8A5D5D;
    }
  }
}
